.about {
  margin-bottom: 30px;
}
@media screen and (max-width: 1023px) {
  .about {
    padding: 0 20px;
  }
}
@media screen and (max-width: 740px) {
  .about {
    margin-bottom: 20px;
  }
}
.about__wrap {
  display: flex;
  flex-wrap: wrap;
}
.about__item--aside {
  width: 628px;
  background-color: #0E1218;
  border-radius: 20px;
  padding: 35px 30px;
  padding-right: 15px;
}
@media screen and (max-width: 1350px) {
  .about__item--aside {
    width: 528px;
  }
}
@media screen and (max-width: 1150px) {
  .about__item--aside {
    width: 100%;
  }
}
@media screen and (max-width: 1023px) {
  .about__item--aside {
    padding: 30px 24px;
  }
}
@media screen and (max-width: 740px) {
  .about__item--aside {
    padding: 15px;
  }
}
.about__item--content {
  width: calc(100% - 628px);
  padding-left: 28px;
}
@media screen and (max-width: 1350px) {
  .about__item--content {
    width: calc(100% - 528px);
  }
}
@media screen and (max-width: 1150px) {
  .about__item--content {
    display: none;
  }
}
.about__numbers {
  position: relative;
  z-index: 1;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 30px 0;
  background-color: #3334FF;
  border-radius: 20px;
  padding: 50px 0;
  border-bottom: 14px solid #3334FF;
}
.about__numbers:after {
  position: absolute;
  content: "";
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #0E1218;
  border-radius: inherit;
}
.about__row {
  display: flex;
  justify-content: space-between;
}
.about__holder {
  display: flex;
  flex-direction: column;
  gap: 20px 0;
  margin-bottom: 40px;
}
@media screen and (max-width: 1023px) {
  .about__holder {
    gap: 26px 0;
    margin-bottom: 30px;
  }
}
.about__avatar {
  max-width: 138px;
  width: 100%;
  flex-grow: 0;
}
@media screen and (max-width: 480px) {
  .about__avatar {
    max-width: 62px;
  }
}
.about__avatar img {
  display: block;
  max-width: 100%;
  border-radius: 50%;
}
.about__title {
  display: flex;
  font-size: 24px;
  color: #fff;
  word-break: break-all;
  font-weight: 700;
}
@media screen and (max-width: 740px) {
  .about__title {
    font-size: 16px;
  }
}
.about__title span {
  display: inline-block;
  min-width: 130px;
  font-weight: 400;
}
@media screen and (max-width: 740px) {
  .about__title span {
    min-width: 88px;
  }
}
.about__inner {
  display: flex;
  align-items: center;
  gap: 22px;
}
@media screen and (max-width: 740px) {
  .about__inner {
    gap: 12px;
  }
}
.about__btn {
  cursor: pointer;
  display: inline-block;
  font-size: 16px;
  line-height: 1.2;
  color: #fff;
  border: none;
  border-radius: 20px;
  background-color: #302EFF;
  padding: 11px 20px;
  font-family: Public Sans, sans-serif;
}
@media screen and (max-width: 1023px) {
  .about__btn {
    font-size: 21px;
    padding: 11px 34px;
  }
}
@media screen and (max-width: 740px) {
  .about__btn {
    font-size: 14px;
    padding: 11px 16px;
  }
}
.about__col {
  position: relative;
  z-index: 1;
  font-size: 24px;
  line-height: 1.3;
  display: flex;
  align-items: center;
  color: #fff;
  height: 50px;
  padding-left: 28px;
  font-weight: 700;
  font-family: DM Sans, sans-serif;
}
@media screen and (max-width: 1350px) {
  .about__col {
    font-size: 22px;
  }
}
.about__col:after {
  position: absolute;
  z-index: -1;
  content: "";
  top: 50%;
  left: 0;
  background-color: rgba(81, 80, 80, 0.5);
  transform: translateY(-50%);
  width: 100%;
  height: 2px;
}
.about__col:before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.about__col:first-child:before {
  background: linear-gradient(90deg, rgba(66, 62, 252, 0) -0.001%, rgb(66, 62, 252) 99.95%);
}
.about__col:nth-child(2):before {
  background: linear-gradient(90deg, rgba(246, 212, 35, 0) 0%, rgb(246, 212, 35) 100%);
}
.about__col:nth-child(3):before {
  background: linear-gradient(90deg, rgba(125, 88, 246, 0) 0%, rgb(125, 88, 246) 100%);
}
.about__col:nth-child(4):before {
  background: linear-gradient(90deg, rgba(54, 57, 61, 0) 0.053%, rgb(54, 57, 61) 100.002%);
}
.ask {
