}
.swiper-balance {
  display: none;
}
@media screen and (max-width: 1023px) {
  .swiper-balance {
    display: block;
    max-width: 707px;
    width: 100%;
    padding: 24px 0px;
    margin: 0 auto;
  }
}
@media screen and (max-width: 740px) {
  .swiper-balance {
    max-width: 450px;
    padding: 20px 0;
  }
}
.swiper-balance.swiper {
  width: 100%;
  height: 100%;
  padding-right: 10%;
  padding-left: 10%;
  margin-left: auto;
  margin-right: auto;
  overflow: visible;
}
.swiper-balance .swiper-slide {
  transform: scale(0.85);
  transition: 0.25s transform;
}
.swiper-balance .swiper-slide-active {
  transform: scale(1);
}
@media screen and (max-width: 740px) {
  .swiper-balance .swiper-button-next,
  .swiper-balance .swiper-rtl .swiper-button-prev {
    right: var(--swiper-navigation-sides-offset, 0px);
  }
}
@media screen and (max-width: 740px) {
  .swiper-balance .swiper-button-prev,
  .swiper-balance .swiper-rtl .swiper-button-next {
    left: var(--swiper-navigation-sides-offset, 0px);
  }
}

