.custom-select__wrapper {
  position: relative;
  z-index: 12;
  max-width: 96px;
  min-width: 96px;
  width: 100%;
}
.custom-select__block {
  position: relative;
  display: inline-block;
  width: 100%;
}
.custom-select__block select {
  display: none;
}
.custom-select__block:before {
  content: attr(data-content);
  display: block;
  width: 100%;
  padding: 9px 10px;
  padding-bottom: 11px;
  color: #fff;
  border-radius: 20px;
  box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.25);
  background-color: #423EFC;
  cursor: pointer;
  text-align: center;
  font-family: Public Sans, sans-serif;
}
.custom-select__block::after {
  position: absolute;
  left: 50%;
  bottom: 2px;
  z-index: 3;
  width: 8px;
  height: 8px;
  content: " ";
  border-right: 2px solid #fff;
  border-bottom: 2px solid #fff;
  pointer-events: none;
  transform: rotate(45deg) translateX(-50%);
  transition: transform 0.3s, top 0.3s;
}
.custom-select__inner:focus + .custom-select::before {
  border-color: #77aaff;
}
.custom-select__options {
  position: fixed;
  z-index: 112;
  left: 0;
  width: 100%;
  max-width: 96px;
  min-width: 96px;
  border-radius: 20px;
  box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.25);
  background: #181A1E;
  padding-bottom: 20px;
  display: none;
}
.custom-select__options::after {
  position: absolute;
  left: 50%;
  bottom: 1px;
  z-index: 3;
  width: 8px;
  height: 8px;
  content: " ";
  border-left: 2px solid #fff;
  border-top: 2px solid #fff;
  pointer-events: none;
  transform: rotate(45deg) translateX(-50%);
  transition: transform 0.3s, top 0.3s;
}
.custom-select__options.visible {
  display: block;
}
.custom-select__item {
  cursor: pointer;
  font-size: 16px;
  padding: 9px 10px;
  padding-bottom: 11px;
  color: #fff;
  text-align: center;
  font-family: Public Sans, sans-serif;
}
.custom-select__item.active {
  background-color: #423EFC;
}
.custom-select__item.active:first-child {
  border-radius: 20px 20px 0 0;
}
.custom-select__item.active:not(:first-child) {
  border-radius: 20px;
}

